import React, { useState, useEffect, useRef,useContext } from "react";
import { Context } from "../../context/Store";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import { Link } from "react-router-dom";
import { FaEdit } from 'react-icons/fa';
import { AiFillDelete } from "react-icons/ai";
const ViewList = () => {
    
    const {comData,serverUrl,transaction,editItem,accountList,getAccounts,accounts,selectTransaction,deleteAcccount} = useContext(Context);

    const [deleteurl, setDeleteurl] = useState(serverUrl+`/deletetransaction`);
    const [findurl, setFindurl] = useState(serverUrl+`/selecttransaction`)

    


    var today = new Date();
    var dd = today.getDate();
    
    var mm = today.getMonth()+1; 
    var yyyy = today.getFullYear();
    if(dd<10) 
    {
        dd='0'+dd;
    } 
    
    if(mm<10) 
    {
        mm='0'+mm;
    } 
    today = yyyy+'-'+mm+'-'+dd;
    
        
        const dateMode = comData.date_mode;
        const serverDate = dateMode==0?today:comData.date;

console.log(transaction);
    const tranData = transaction.filter(
      (element) => element.date ===serverDate  && element.others_c==='ITRAN'
    );

    const columns = [
     
        {
          dataField: "inv_id",
          text: "INV ID",
          footer: '',
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "name",
          text: "Name",
          footer: '',
          headerStyle: () => {
            return { width: "25%" };
          },
        },
        {
          dataField: "shead",
          text: "A/C TYPE",
          footer: '',
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "payment",
          text: "Payment",
          footer: payment => payment.reduce((acc, item) => acc + item, 0),
          headerStyle: () => {
            return { width: "10%" , align: 'right'};
          },
        },
        {
          dataField: "receive",
          text: "Receive",
          footer: '45555555555',
          headerStyle: () => {
            return { width: "10%" , align: 'right'};
          },
        },
        {
          dataField: "comments",
          text: "Comments",
          footer: '',
          headerStyle: () => {
            return { width: "20%" };
          },
        },
    
        {
          dataField: "id",
          text: "Action",
          footer: '',
          formatter: (rowContent, row) => {
            return (
              <div>
                
                <Link
                  // to={`/edit/${row.id}`}
                  onClick={() => selectTransaction(row.id,findurl)}
                  color="warning"
                  className=""
                >
                   <FaEdit />
                </Link>
               
    
                <Link
                onClick={() => deleteAcccount(row.id,deleteurl)}
                  color="danger"
                  className=""
                  >
                   <AiFillDelete />
                </Link>
              </div>
            );
          },
        },
      ];
      const rowStyle = (row, rowIndex) => {
        return { overflowWrap: "break-word", padding: "0px" };
      };
    
    
    const MySearch = (props) => {
      let input;
      const handleClick = () => {
        props.onSearch(input.value);
      };

// useEffect(() => {
//   if(editItem){
//     getAccounts();
  
   
// })

      
      return (
        <div>
          <input
          onChange={ handleClick }
            className="form-control"
            style={ { backgroundColor: 'white' } }
            ref={ n => input = n }
            type="text"
          />
          {/* <button className="btn btn-warning" onClick={ handleClick }>Click to Search!!</button> */}
        </div>
      );
    };







    return (
        <>
          
          <section className="col-lg-8 connectedSortable">
              <div className="box box-primary">
                <div className="box-header header-custom">
                  <h3 onClick={getAccounts} className="box-title">Detail Info</h3>
                </div>
                {/* /.box-header */}
                <div className="box-body">
                  <ToolkitProvider
                    keyField="id"
                    data={tranData}
                    columns={columns}
                    search
                    
                  >
                    {(props) => (
                      <div>
                        <MySearch { ...props.searchProps } />

                        <BootstrapTable
                          {...props.baseProps}
                          loading={ true }
                          classes="table_tr"
                          pagination={paginationFactory({ sizePerPage: 10 })}
                          rowStyle={rowStyle}
                          headerClasses="header-class"
                          headerStyle
                          
                        />
                      </div>
                    )}
                  </ToolkitProvider>
                </div>
                {/* /.box-body */}
              </div>
              {/* /.box */}
            </section>  
        </>
    )
}

export default ViewList
