import React, { useState, useEffect, useRef, useContext } from "react";
import { Context } from "../../context/Store";
import AclistComponent from "../../components/AclistComponent";
import Select from "react-select";
import { useDispatch, useSelector } from "react-redux";
import {
  getAccounts,
  getAlllc,
  getAllsell,
  getLcproduct,
} from "../../store/getDataSlice";
import { saveData, updateData, selectData } from "../../store/lcopeningSlice";

const AddForm = () => {
  const { lc_data, edit} = useSelector((state) => state.lcaccount)
  const dispatch = useDispatch();
  const {
    optionSelectedexp,
    setOptionSelectedexp,
    optionSelectedimp,
    // edit,
    setOptionSelectedimp,
    serverUrl,
    editItem,
    dataInsert,
    getAccounts,
    accountList,
    notify,
    ToastContainer,
    shead,
    getShead,
    selectAcccount,
    setEdit,

    deleteAcccount,
    inputRef,
  } = useContext(Context);

  const [addurl, setAddurl] = useState(serverUrl + `/createlcac`);
  const [updateurl, setUpdateurl] = useState(serverUrl + `/update`);

  const [lcopen, setLcopen] = useState({
    id:"",
    lc_no: "",
    qty: "",
    rate: "",
    l_date: "",
    imp: "",
    imp_name: "",
    exp: "",
    exp_name: "",
    country: "",
  });

  let name, value;
  const getUserData = (event) => {
    name = event.target.name;
    value = event.target.value;

    setLcopen({ ...lcopen, [name]: value });
  };

  const billChng = (event) => {
    setLcopen({
      ...lcopen,
      country: event.target.value,
    });
  };

  const impchange = (selectedOption) => {
    console.log("Selected city", selectedOption.value);
    setLcopen({
      ...lcopen,
      imp: selectedOption.value,
      imp_name: selectedOption.label,
    });
    setOptionSelectedimp({
      ...optionSelectedimp,
      value: selectedOption.value,
      label: selectedOption.label,
    });
    // payFocus.current.focus();
  };
  const expchange = (selectedOption) => {
    setLcopen({
      ...lcopen,
      exp: selectedOption.value,
      exp_name: selectedOption.label,
    });
    setOptionSelectedexp({
      ...optionSelectedexp,
      value: selectedOption.value,
      label: selectedOption.label,
    });
  };
  const addAccounts = (e) => {
    if (!editItem) {
      e.preventDefault();
      dispatch(saveData(lcopen));
      // dataInsert(addurl, lcopen);
      setLcopen({
        lc_no: "",
        qty: "",
        rate: "",
        l_date: "",
        imp: "",
        imp_name: "",
        exp: "",
        exp_name: "",
        country: "",
      });
      getAccounts();
    }
  };

  const updateAccount = (e) => {
    if (edit) {
      e.preventDefault();
      dispatch(updateData(lcopen));
      setLcopen({
        lc_no: "",
        qty: "",
        rate: "",
        l_date: "",
        imp: "",
        imp_name: "",
        exp: "",
        exp_name: "",
        country: "",
      });
      setOptionSelectedimp({
        ...optionSelectedimp,
        value: "",
        label:"Select imp",
      });
      setOptionSelectedexp({
        ...optionSelectedexp,
        value: "",
        label: "Select exp",
      });
      getAccounts();
    }
  };

  useEffect(() => {
    if (edit) {
      // setLcopen(lc_data[0]);
      setLcopen({...lcopen,
        id:lc_data[0].id,
        lc_no: lc_data[0].lc_no,
        qty: lc_data[0].qty,
        rate: lc_data[0].rate,
        l_date: lc_data[0].l_date,
        imp: lc_data[0].imp_id,
        imp_name:lc_data[0].imp_account_name,
        exp: lc_data[0].exp_id,
        exp_name: lc_data[0].exp_account_name,
        country: lc_data[0].country,
      });
      // console.log(lcopen);

      setOptionSelectedimp({
        ...optionSelectedimp,
        value: lc_data[0].imp_id,
        label: lc_data[0].imp_account_name,
      });
      setOptionSelectedexp({
        ...optionSelectedexp,
        value: lc_data[0].exp_id,
        label: lc_data[0].exp_account_name,
      });
    }
    getAccounts();
  }, [edit]);
  useEffect(() => {
    dispatch(getAlllc())
      }, [dispatch]);
    
  return (
    <>
      <section className="col-lg-3 connectedSortable">
        <div className="box box-danger">
          {/* Tabs within a box */}
          <div className="tab-content no-padding">
            {/* Morris chart - Sales */}
            <div className="box-header header-custom">
              <h3 className="box-title">Account Info</h3>
            </div>
            <form
              className="form-horizontal"
              id="fupForm"
              name="form1"
              method="post"
            >
              <div className="box-body">
                <div className="form-group">
                  {/*<label for="accntno" className="col-sm-4 control-label">Id</label>*/}
                  <div className="col-sm-12">
                    <input
                      required
                      className="form-control"
                      type="text"
                      value={lcopen.lc_no}
                      name="lc_no"
                      placeholder="Lc no"
                      autocomplete="off"
                      onChange={getUserData}
                      id="lc_no"
                    />
                  </div>
                </div>
            
                <div className="form-group">
                  {/*<label for="proprietor" className="col-sm-4 control-label">Proprietor</label>*/}
                  <div className="col-sm-12">
                    <input
                      required
                      className="form-control"
                      type="text"
                      value={lcopen.qty}
                      name="qty"
                      placeholder="Qty"
                      autocomplete="off"
                      onChange={getUserData}
                      id=""
                    />
                  </div>
                </div>
                <div className="form-group">
                  {/*<label for="subaddress" className="col-sm-4 control-label">Address</label>*/}
                  <div className="col-sm-12">
                    <input
                      required
                      className="form-control"
                      type="text"
                      value={lcopen.rate}
                      name="rate"
                      placeholder="Rate"
                      autocomplete="off"
                      onChange={getUserData}
                      id=""
                    />
                  </div>
                </div>
                <div className="form-group">
                  {/*<label for="address" className="col-sm-4 control-label">Route</label>*/}
                  <div className="col-sm-12">
                    <input
                      required
                      className="form-control"
                      type="text"
                      value={lcopen.l_date}
                      name="l_date"
                      placeholder="Last Date"
                      autocomplete="off"
                      onChange={getUserData}
                      id=""
                    />
                  </div>
                </div>

                <div className="form-group">
                  {/*<label for="address" className="col-sm-4 control-label">Route</label>*/}
                  <div className="col-sm-12">
                    <AclistComponent
                      actype={"IMPORTER"}
                      onChange={impchange}
                      edit={edit}
                      optionlcclient={optionSelectedimp}
                    />
                    {/* <Select
                     
                  
// isDisabled={edit}

//                      value={optionSelectedimp}
//                      formatOptionLabel={formatOptionLabel}
//                      options={options}
                    
                     // defaultValue={options[1]}
                   /> */}
                  </div>
                </div>
                <div className="form-group">
                  {/*<label for="address" className="col-sm-4 control-label">Route</label>*/}
                  <div className="col-sm-12">
                    <AclistComponent
                      actype={"EXPORTER"}
                      onChange={expchange}
                      edit={edit}
                      optionlcclient={optionSelectedexp}
                    />
                  </div>
                </div>

                <div className="form-group">
                  {/*<label for="type" className="col-sm-4 control-label">Account Type</label>*/}
                  <div className="col-sm-12">
                    <select
                      required
                      value={lcopen.country}
                      onChange={billChng}
                      name="country"
                      autocomplete="off"
                      id="country"
                      className="form-control"
                    >
                      <option value=""> -- Country -- </option>
                      {/* Mapping through each fruit object in our fruits array
                 and returning an option element with the appropriate attributes / values.
                */}
                      <option value="India">India</option>
                      <option value="Bhutan">Bhutan</option>
                    </select>
                  </div>
                </div>
              </div>

              {/* /.box-body */}
              <div className="box-footer">
                <button type="submit" className="btn btn-danger">
                  Cancel
                </button>
                <input
                  type="button"
                  name="save"
                  className="btn btn-success pull-right"
                  defaultValue={edit ? "Update" : "Save"}
                  id="butsave"
                  onClick={edit ? updateAccount : addAccounts}
                />
              </div>
              {/* /.box-footer */}
            </form>
          </div>
        </div>
      </section>
    </>
  );
};

export default AddForm;
