import React,{useEffect} from 'react';
import Select from 'react-select';
import { useDispatch, useSelector } from "react-redux"
import { getAccounts } from"../store/getDataSlice";
const customStyles = {
  option: (provided, state) => ({
    ...provided,
    borderBottom: '1px dotted gray',
    color: state.isSelected ? 'red' : 'black',
    padding: 20,
  }),
};

const SideList = ({onChange,optionlcclient,edit,uref}) => {
  // const {  error,loading ,account_list,status} = useSelector((state) => state.getdata)
  // console.log(loading);
  // const dispatch = useDispatch();
  const {  side_list,error,loading ,account_list,status} = useSelector((state) => state.getdata)
 
console.log(side_list);
  const acoptions = [];
  side_list.forEach((e) => {
    acoptions.push({
      value: e.id,
      label: e.name,
      customAbbreviation: e.type,
      country: e.type,
    });
  });
  const formatacOptionLabel = (acoptions, { context }) =>
  context === "value" ? (
    <div
      className=""
      style={{ display: "flex", justifyContent: "space-between" }}
    >
      <div style={{ fontWeight: "bold", fontSize: "13px" }}>
        {acoptions.label}
      </div>
      <div
        style={{
          fontWeight: "bold",
          color: "red",
          fontStyle: "italic",
          fontSize: "13px",
        }}
      >
        {acoptions.customAbbreviation}
      </div>
      <div style={{ marginLeft: "10px", color: "#ccc", fontSize: "12px" }}>
        {acoptions.Atype}
      </div>
    </div>
  ) : (
    <>
      <div className="" style={{ display: "flex", fontSize: "12px" }}>
        <img
          class="avatar"
          src="https://pos.softghor.com/dashboard/img/avatar/1.jpg"
          alt="avater"
        ></img>

        <div className="" style={{ display: "flex", fontSize: "12px" }}>
          <div>{acoptions.label}</div>
          <div
            style={{ marginLeft: "10px", color: "#ccc", fontSize: "12px" }}
          >
            {acoptions.customAbbreviation}
          </div>
          <div
            style={{ marginLeft: "10px", color: "#ccc", fontSize: "12px" }}
          >
            {acoptions.Atype}
          </div>
        </div>
      </div>
    </>
  );
  console.log(uref);
  return (
    
    <Select
      ref={uref}
      options={acoptions}
      onChange={onChange}
      formatOptionLabel={formatacOptionLabel}
      value={optionlcclient}
      defaultValue={acoptions[1]}
      isDisabled={edit ? true : false}
    />
  );
};

export default SideList;