import React, { useState, useEffect, useRef,useContext } from "react";
import { Context } from "../../context/Store";
import Select from "react-select";
const AddForm = () => {
    
const {comData,getComdata,serverUrl,setOptionSelectedtransaction,optionSelectedtransaction, editItem,dataInsert,getAccounts,accountList,notify,ToastContainer,shead,getShead,selectAcccount,edit,setEdit,updateData,deleteAcccount,inputRef} = useContext(Context);

const [addurl, setAddurl] = useState(serverUrl+`/createtransaction`)
const [updateurl, setUpdateurl] = useState(serverUrl+`/updatetransaction`)
const productFocus = useRef();
const payFocus = useRef();

var today = new Date();
var dd = today.getDate();

var mm = today.getMonth()+1; 
var yyyy = today.getFullYear();
if(dd<10) 
{
    dd='0'+dd;
} 

if(mm<10) 
{
    mm='0'+mm;
} 
today = yyyy+'-'+mm+'-'+dd;

    
    const dateMode = comData.date_mode;
    const serverDate = dateMode==0?today:comData.date;
    // const [count, setCount] = useState(1);
  console.log(serverDate);
 

const [accounts, setAccounts] = useState({
  date: serverDate,
  cus_id: "",
  name: "",
  payment: ".00",
  receive: ".00",
  comments: "",
  posted: "ja-471",
});
const myNewInputData = {
  date: serverDate,
  cus_id: accounts.cus_id,
  name: accounts.name,
  payment: accounts.payment,
  receive: accounts.receive,
  comments: accounts.comments,
  posted: accounts.posted,
};

let name, value;
const getUserData = (event) => {
  name = event.target.name;
  value = event.target.value;

  setAccounts({ ...accounts, [name]: value });
};
const handleButtonClick = (e) => {
  const num = Number('111111'.replace(/,/g, ''));
  const result = num.toString();
  e.preventDefault();
  const formattedValue = Number(accounts.payment).toLocaleString('en-US', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  });
  setAccounts({ ...accounts, payment: formattedValue });
};

const options = [];
accountList.forEach((e) => {
  options.push({
    value: e.id,
    label: e.name,
    customAbbreviation: e.due,
  });
});

const formatOptionLabel = (options, { context }) =>
  context === "value" ? 
  
  <div className="" style={{ display: "flex", justifyContent:"space-between", }}>
   <div  style={{ fontWeight:"bold", fontSize: "13px" }}>{options.label}</div>
  <div style={{ fontWeight:"bold", color: "red",fontStyle: 'italic',fontSize: "13px" }}>
    {options.customAbbreviation}
  </div>
   </div>
  : 
  
  
  <>
 <div className="" style={{ display: "flex", fontSize: "12px" }}>
   
   <img class="avatar" src="https://pos.softghor.com/dashboard/img/avatar/1.jpg" alt="avater"></img>
   
   <div className="" style={{ display: "flex", fontSize: "12px" }}>
   <div>{options.label}</div>
  <div style={{ marginLeft: "10px", color: "#ccc",fontSize: "12px" }}>
    {options.customAbbreviation}
  </div>
   </div>
   
 </div>
</>

const selectText = (props) => {
  const input = document.getElementById(props);
  input.focus();
  input.select();
};
const addAccounts = async (e)=>{
  console.log(accounts)
  e.preventDefault();
  if (!editItem) {
  e.preventDefault();
  await dataInsert(addurl,myNewInputData);
  setAccounts({
    date:"",
    cus_id: "",
    name: "",
    payment: "0",
    receive: "0",
    comments: "",
    posted: "ja-789",
  });
  // await getComdata();
  await productFocus.current.focus();
  setOptionSelectedtransaction({
    ...optionSelectedtransaction,
    value: "", label: "" 
  })
}
}

const updateAccount = async(e)=>{
  e.preventDefault();
  if (editItem) {
  e.preventDefault();
  updateData(updateurl,myNewInputData);
  setAccounts({
    date: "",
    cus_id: "",
    name: "",
    payment: "0",
    receive: "0",
    comments: "",
  });
  await productFocus.current.focus();
  // getAccounts()
}
}

const handleEnter = (event) => {
  if (event.key.toLowerCase() === "enter") {
    
    const form = event.target.form;
    const index = [...form].indexOf(event.target);
    form.elements[index + 1].focus();
    event.preventDefault();
  }
};


useEffect(() => {
  productFocus.current.focus();
  if (editItem) {
    setAccounts(editItem)
    console.log(editItem)
  } 
 
}, [editItem])

    return (
        <>
           <section className="col-lg-4 connectedSortable">
              <div className="box box-danger">
                <div className="tab-content no-padding">
                  <div className="box-header header-custom">
                    <h3 className="box-title">Account Info-{serverDate}</h3>
                  </div>
                  <form
                    className="form-horizontal"
                    id="fupForm"
                    name="form1"
                    method="post"
                  >
                    <div className="box-body">   
                  

                      <div className="form-group">
                        <label for="name" className="col-sm-4 control-label">Account Name</label>
                        <div className="col-sm-8">
                        <Select
                          ref={productFocus}
                      onFocus={()=> getAccounts()}
                      onChange={(selectedOption) => {
                        console.log("Selected city", selectedOption.value);
                        setAccounts({
                          ...accounts,
                          name: selectedOption.value,
                        });
                        setOptionSelectedtransaction({
                          ...optionSelectedtransaction,
                          value: selectedOption.value, label: selectedOption.label 
                        })
                        payFocus.current.focus();
                        
                      }}
isDisabled={edit}

                      value={optionSelectedtransaction}
                      formatOptionLabel={formatOptionLabel}
                      options={options}
                     
                      // defaultValue={options[1]}
                    />
                        </div>
                      </div>
                      <div className="form-group">
                        <label for="payment" className="col-sm-4 control-label">Payment</label>
                        <div className="col-sm-8">
                          <input
                            onFocus={() => {
                              selectText("payment");
                              
                            }}
                           ref={payFocus}
                             
                            className="form-control"
                            type="text"
                            value={accounts.payment}
                            name="payment"
                            placeholder="Payment"
                            autocomplete="off"
                            onKeyDown={handleEnter}
                            onChange={getUserData}
                            id="payment"
                            style={{ fontSize: 14, fontWeight: "bold",textAlign:"right"  }}
                          />
                        </div>
                      </div>
                      <div className="form-group">
                        <label for="receive" className="col-sm-4 control-label">Receive</label>
                        <div className="col-sm-8">
                          <input
                           onFocus={() => {
                            selectText("receive");
                            
                          }}
                             
                            className="form-control"
                            type="number"
                            onKeyDown={handleEnter}
                            value={accounts.receive}
                            name="receive"
                            placeholder="Receive"
                            autocomplete="off"
                            onChange={getUserData}
                            id="receive"
                            style={{ fontSize: 14, fontWeight: "bold",textAlign:"right"  }}
                          />
                        </div>
                      </div>
                      <button onClick={handleButtonClick}>Format</button>
                      <div className="form-group">
                        <label for="comments" className="col-sm-4 control-label">Comments</label>
                        <div className="col-sm-8">
                          <input
                           onFocus={() => {
                            selectText("comments");
                            
                          }}
                             
                            className="form-control"
                            type="text"
                            value={accounts.comments.toUpperCase()}
                            name="comments"
                            placeholder="Comments"
                            autocomplete="off"
                            onKeyDown={handleEnter}
                            onChange={getUserData}
                            id="comments"
                          />
                        </div>
                      </div>
                     
                      
                    </div>

                    {/* /.box-body */}
                    <div className="box-footer">
                      <button type="submit"  onKeyDown={handleEnter} className="btn btn-danger">
                        Cancel
                      </button>
                      <input
                        type="button"
                        name="save"
                        className="btn btn-success pull-right"
                        defaultValue={edit ? "Update" : "Save"}
                        id="butsave"
                        onClick={edit ? updateAccount : addAccounts}
                      />
                    </div>
                    {/* /.box-footer */}
                  </form>
                </div>
              </div>
            </section>  
        </>
    )
}

export default AddForm
