import React, { useState, useEffect, useRef, useContext } from "react";
import { Context } from "../../context/Store";
import "react-responsive-modal/styles.css";
import "react-toastify/dist/ReactToastify.css";
import SideList from "../../components/SideList";
import AclistComponent from "../../components/AclistComponent";
import { Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux"
import { getAccounts,getAllsell,getAllside } from "../../store/getDataSlice";
import { saveData ,updateData} from "../../store/supplySlice";
import swal from "sweetalert";
const AddForm = () => {
  const dispatch = useDispatch();
  const [initialCheck, setInitialcheck] = useState("CASH");
 
  const {error,loading ,account_list,status} = useSelector((state) => state.getdata);
  const {supply_cart,edit,refresh} = useSelector((state) => state.supply)
  const {
    setIsLoading,
    isLoading,
    getsellData,
    setEdit,
    comData,

    // edit,
  } = useContext(Context);
  const sellType = useRef();
  const vehicleFocus = useRef();
  const weightFocus = useRef();
  const sideFocus = useRef();
  const clientFocus = useRef();
  const paidFocus = useRef();
  const [optionSide, setOptionside] = useState( { value: "", label: "Select Side..." });
  const [optionlcclient, setOptionlcclient] = useState( { value: "", label: "Select Account..." });
  // const [url, setUrl] = useState(`/allsell`);
  // const [upurl, setUpurl] = useState(serverUrl + `/allsellupdate`);

  let name, value;
  const getUserData = (event) => {
    name = event.target.name;
    value = event.target.value;
    setSupplystore({ ...supplystore, [name]: value });
  };

  // const suma=() =>{
  //   var w_t = Number(document.getElementById("ton").value)
  // }


  const billChng = (event) => {
    setInitialcheck(event.target.value);
    if (event.target.value === "CASH") {
      setIsLoading(false);
      setSupplystore({
        ...supplystore,
        client_id: 1,
        bill_type: event.target.value,
      });
    } else if (event.target.value === "CREDIT") {
      setIsLoading(true);
      setSupplystore({
        ...supplystore,
        bill_type: event.target.value,
      });
    } else {
    }
  };



  const [optionSelectedList, setOptionSelectedList] = useState({
    value: "",
    label: "Select Vehicle..",
  });

  // const vehicles = lpvehicle.filter(
  //   (vehicle) => vehicle.lc_no == supplystore.lc_id
  // );

  var today = new Date();
  var dd = today.getDate();

  var mm = today.getMonth() + 1;
  var yyyy = today.getFullYear();
  if (dd < 10) {
    dd = "0" + dd;
  }

  if (mm < 10) {
    mm = "0" + mm;
  }
  today = yyyy + "-" + mm + "-" + dd;

  const dateMode = comData.date_mode;
  const serverDate = dateMode == 0 ? today : comData.date;
  const [supplystore, setSupplystore] = useState({
    id:"",
    inventory:"CRASH",
    bill_type:"CASH",
    sell_type:"",
    date:today,
    client_id: "1",
    client_name:"",
    side:"",
    side_name:"",
    v_no:"",
    size:"",
    ton:"0",
    cft:"0",
    rate:"0",
    total:"0",
    ld_un:"0",
    transport:"0",
    paid:"0",
    g_total:"0",
    chalan:"N/A",
    particular:"N/A",
    posted: "ja-471",
  });
  
  const selectText = (props) => {
    const input = document.getElementById(props);
    input.focus();
    input.select();
  };

  const insertData = async (e) => {
    // const actype = supplystore.client_type;
    e.preventDefault();

    if (supplystore.rate == 0 || supplystore.sell_type=="") {
      swal({
        title: "Warning!",
        text: "Fill Up all data!!",
        icon: "warning",
        timer: 2500,
        button: false
      })
    } else {
     edit? dispatch(updateData(supplystore)):
     dispatch(saveData(supplystore));
      await getsellData();
      await setEdit(false);
      swal({
        title: "Done!",
        text: "Data inserted successfully!!",
        icon: "success",
        timer: 2500,
        button: false
      })
      await sellType.current.focus()
    }

    await resetLp();
   dispatch(getAllsell());
  };

  const resetLp = async () => {
    setSupplystore({
      ...supplystore,
      sell_type: "",
      bill_type: "CASH",
      client_id: "1",
      client_name: "",
      side: "",
      side_name: "",
      v_no: "",
      size: "",
      ton: "0",
      cft: "0",
      rate: "0",
      total: "0",
      ld_un: "0",
      transport: "0",
      paid: "0",
      g_total: "0",
      chalan: "N/A",
      particular: "N/A",
      posted: "ja-471",
    });

    getsellData();
    setOptionside({ value: "", label: "Select Side..." });
    setOptionlcclient({ value: "", label: "Select Account..." });
    // setOptionSelectedaccount({ value: "", label: "Select Side.." });
    setOptionSelectedList({ value: "", label: "Select Type.." });
    setIsLoading(false);
    setInitialcheck("CASH");
    // (await supplystore.bill_type) === "CASH"
    //   ? sellType.current.focus()
    //   : clientFocus.current.focus();
  };
  const handleEnter = (event) => {
    if (event.key.toLowerCase() === "enter") {
      const form = event.target.form;
      const index = [...form].indexOf(event.target);
      form.elements[index + 1].focus();
      event.preventDefault();
    }
  };

  const handleEnter2 = (event) => {
    if (event.key.toLowerCase() === "enter") {
      const form = event.target.form;
      const index = [...form].indexOf(event.target);
      form.elements[index + 2].focus();
      event.preventDefault();
    }
  };

  const slectchange = (selectedOption) => {
    setSupplystore({
      ...supplystore,
      client_id: selectedOption.value,
      client_name: selectedOption.label,
    });
    setOptionlcclient({
      ...optionlcclient,
      value: selectedOption.value,
      label: selectedOption.label,
    });
    sideFocus.current.focus();
  };

  const slectchangeside = (selectedOption) => {
    setSupplystore({
      ...supplystore,
      side: selectedOption.value,
      side_name: selectedOption.label,
    });
    setOptionside({
      ...optionSide,
      value: selectedOption.value,
      label: selectedOption.label,
    });
    vehicleFocus.current.focus();
  };
  useEffect(() => {

if(edit===true){
  setInitialcheck(supply_cart[0].bill_type)
  setOptionlcclient({
    ...optionlcclient,
    value: supply_cart[0].client_id,
    label: supply_cart[0].name
  });
setSupplystore({...supplystore,
id:supply_cart[0].id,
inventory:supply_cart[0].inventory,
bill_type:supply_cart[0].bill_type,
sell_type:supply_cart[0].sell_type,
date:supply_cart[0].date,
client_id:supply_cart[0].client_id,
client_name:supply_cart[0].client_name,
side:supply_cart[0].side,
side_name:supply_cart[0].side_name,
v_no:supply_cart[0].v_no,
size:supply_cart[0].type,
ton:supply_cart[0].ton,
cft:supply_cart[0].cft,
rate:supply_cart[0].rate,
total:supply_cart[0].total,
ld_un:supply_cart[0].ld_un,
transport:supply_cart[0].transport,
paid:supply_cart[0].paid,
g_total:supply_cart[0].g_total,
chalan:supply_cart[0].chalan,
particular:supply_cart[0].particular,
posted: "ja-471",
})
dispatch(getAllsell());
} else{
  setInitialcheck("CASH")
}
  }, [edit]);

  useEffect(() => {
    
    const total =  Number(supplystore.cft) * supplystore.rate ;
  
    const g_total =total+ Number(supplystore.ld_un) +  Number(supplystore.transport)- Number(supplystore.paid);
    setSupplystore({ ...supplystore, total: total,g_total:g_total });
  }, [supplystore.cft, supplystore.rate, supplystore.ld_un, supplystore.transport, supplystore.paid]);

  useEffect(() => {
    dispatch(getAccounts());
  dispatch(getAllsell());
  dispatch(getAllside());
  }, [refresh]);


  return (
    <>
   
      <section className="col-lg-7 connectedSortable">
        <div className="box box-danger">
          {/* Tabs within a box */}
          <div className="tab-content no-padding">
            {/* Morris chart - Sales */}
            <div className="box-header header-custom">
              <h3 className="box-title">
                <i className="fa fa-address-book"></i> Details
              </h3>
              <h3 className="box-title pull-right">
                Date : <strong>{serverDate}</strong>
              </h3>
            </div>

            <form>
              <div className="box-body form-horizontal">
                <div class="row">
                  <div class="col-md-6">
                    <div className="form-group ">
                      <label
                        htmlFor="accntname"
                        className="col-sm-3 control-label"
                      >
                        Bill Type
                      </label>
                      <div className="  col-sm-8">
                        <label>
                          <input
                            type="radio"
                            disabled={edit ? true : false}
                            value="CASH"
                            name="billtype"
                            checked={initialCheck === "CASH"}
                            onChange={billChng}
                            // onChange={this.handleOptionChange}
                          />
                          CASH
                        </label>
                        <label>
                          <input
                            type="radio"
                            disabled={edit ? true : false}
                            value="CREDIT"
                            name="billtype"
                            checked={initialCheck === "CREDIT"}
                            onChange={(getUserData, billChng)}
                          />
                          CREDIT
                        </label>
                      </div>
                    </div>

                    <div className="form-group ">
                      <label
                        htmlFor="accntname"
                        className="col-sm-3 control-label"
                      >
                        Sell Type
                      </label>
                      <div className="  col-sm-8">
                        <select
                       value={supplystore.sell_type}
                          ref={sellType}
                          style={{ width: "100%" }}
                          className="form-control"
                          disabled={edit ? true : false}
                          name="sell_type"
                          placeholder="sell_type"
                          autocomplete="off"
                          onChange={getUserData}
                          onKeyDown={isLoading ? handleEnter : handleEnter2}
                          // disabled={isCart}
                        >
                          <option value="">
                            Select
                          </option>
                          <option value="PUR">PURCHASE</option>
                          <option value="SELL">SELL</option>
                        </select>
                      </div>
                    </div>
                    <div
                      id="divsup"
                      style={{ display: initialCheck==='CREDIT' ? "block" : "none" }}
                      className="form-group"
                    >
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Account
                      </label>

                      <div className="col-sm-8">
                        <AclistComponent
                          onChange={slectchange}
                          optionlcclient={optionlcclient}
                          edit={edit}
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label htmlFor="side" className="col-sm-3 control-label">
                        Side
                      </label>

                      <div className="col-sm-8">
                      <SideList
                          uref={sideFocus}
                          onChange={slectchangeside}
                          optionlcclient={optionSide}
                          edit={edit}
                        />
                      </div>
                    </div>


                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Vehicle
                      </label>

                      <div className="col-sm-8">
                        <input
                          ref={vehicleFocus}
                          onFocus={() => {
                            selectText("v_no");
                          }}
                          onKeyDown={handleEnter}
                          className="form-control"
                          type="text"
                          value={supplystore.v_no.toUpperCase()}
                          name="v_no"
                          placeholder=""
                          autocomplete="off"
                          onChange={getUserData}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="v_no"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Size
                      </label>

                      <div className="col-sm-8">
                        <input
                          ref={weightFocus}
                          onFocus={() => {
                            selectText("size");
                          }}
                          onKeyDown={handleEnter}
                          className="form-control"
                          type="text"
                          value={supplystore.size}
                          name="size"
                          placeholder=""
                          autocomplete="off"
                          onChange={getUserData}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="size"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Ton
                      </label>

                      <div className="col-sm-8">
                        <input
                          ref={weightFocus}
                          onFocus={() => {
                            selectText("ton");
                          }}
                          // onChange={total}
                          onKeyDown={handleEnter}
                          className="form-control"
                          type="number"
                          value={supplystore.ton}
                          name="ton"
                          placeholder="Ton"
                          autocomplete="off"
                          onChange={(getUserData)}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="ton"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Cft
                      </label>

                      <div className="col-sm-8">
                        <input
                          ref={weightFocus}
                          onFocus={() => {
                            selectText("cft");
                          }}
                          // onChange={total}
                          onKeyDown={handleEnter}
                          className="form-control"
                          type="number"
                          value={supplystore.cft}
                          name="cft"
                          placeholder="Cft"
                          autocomplete="off"
                          onChange={(getUserData)}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="cft"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Rate
                      </label>
                      <div className="col-sm-8">
                        <input
                          onFocus={() => {
                            selectText("rate");
                          }}
                          className="form-control"
                          type="number"
                          onKeyDown={handleEnter}
                          value={supplystore.rate}
                          name="rate"
                          placeholder=""
                          autocomplete="off"
                          onChange={(getUserData)}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="rate"
                        />
                      </div>
                    </div>
                  </div>
                  <div class="col-md-6">
                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Sub Total
                      </label>
                      <div className="col-sm-8">
                        <input
                          onFocus={() => {
                            selectText("total");
                          }}
                          readOnly={true}
                          className="form-control fclr"
                          type="number"
                          value={supplystore.total}
                          onKeyDown={handleEnter}
                          name="total"
                          placeholder=""
                          autocomplete="off"
                          // disabled="true"
                          onChange={getUserData}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="total"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Load/un
                      </label>

                      <div className="col-sm-8">
                        <input
                          ref={weightFocus}
                          onFocus={() => {
                            selectText("ld_un");
                          }}
                          // disabled={edit ? true : false}
                          onKeyDown={handleEnter}
                          className="form-control"
                          type="number"
                          value={supplystore.ld_un}
                          name="ld_un"
                          placeholder="Weight"
                          autocomplete="off"
                          onChange={(getUserData)}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="ld_un"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Transport
                      </label>

                      <div className="col-sm-8">
                        <input
                          ref={weightFocus}
                          onFocus={() => {
                            selectText("transport");
                          }}
                          // disabled={edit ? true : false}
                          onKeyDown={isLoading ? handleEnter : handleEnter2}
                          className="form-control"
                          type="number"
                          value={supplystore.transport}
                          name="transport"
                          placeholder=""
                          autocomplete="off"
                          onChange={(getUserData)}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="transport"
                        />
                      </div>
                    </div>

                    <div
                      id="divsup"
                      style={{ display: isLoading ? "block" : "none" }}
                      className="form-group"
                    >
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Paid
                      </label>

                      <div className="col-sm-8">
                        <input
                          // disabled={edit ? true : false}
                          ref={paidFocus}
                          onFocus={() => {
                            selectText("paid");
                          }}
                          onKeyDown={handleEnter}
                          className="form-control"
                          type="number"
                          value={supplystore.paid}
                          name="paid"
                          placeholder=""
                          autocomplete="off"
                          onChange={(getUserData)}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="paid"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Total
                      </label>

                      <div className="col-sm-8">
                        <input
                          ref={weightFocus}
                          onFocus={() => {
                            selectText("g_total");
                          }}
                          onKeyDown={handleEnter}
                          className="form-control fclr"
                          readOnly={true}
                          type="number"
                          value={supplystore.g_total}
                          name="g_total"
                          placeholder=""
                          autocomplete="off"
                          onChange={getUserData}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="g_total"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Chalan{" "}
                      </label>

                      <div className="col-sm-8">
                        <input
                          ref={weightFocus}
                          onFocus={() => {
                            selectText("chalan");
                          }}
                          onKeyDown={handleEnter}
                          className="form-control"
                          type="text"
                          value={supplystore.chalan}
                          name="chalan"
                          placeholder=""
                          autocomplete="off"
                          onChange={getUserData}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="chalan"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="form-group">
                      <label
                        htmlFor="cust_id"
                        className="col-sm-3 control-label"
                      >
                        Particular
                      </label>
                      <div className="col-sm-8">
                        <input
                          onFocus={() => {
                            selectText("particular");
                          }}
                          onKeyDown={handleEnter}
                          className="form-control"
                          type="text"
                          value={supplystore.particular}
                          name="particular"
                          placeholder=""
                          autocomplete="off"
                          onChange={getUserData}
                          style={{
                            fontSize: 14,
                            fontWeight: "bold",
                            textAlign: "right",
                          }}
                          id="particular"
                        />
                      </div>
                    </div>

                    <div id="divsup" className="pull-right">
                      <Button onClick={insertData} variant={edit ? "warning" : "success"}>
                        {edit ? "Update" : "Submit"}
                      </Button>{" "}
                      {/* <Button variant="danger" onClick={editdb}>
                          Submit
                            </Button>{" "} */}
                    </div>
                  </div>

                  {/* /.col */}
                </div>
                <style
                  type="text/css"
                  dangerouslySetInnerHTML={{
                    __html:
                      "\n fieldset {\n  padding: 0;\n  margin: 0;\n  border: 0;\n\n  min-width: 0;\n}\n\nlegend {\n  display: block;\n  width: 100%;\n  padding: 0;\n  margin-bottom: @line-height-computed;\n  font-size: (@font-size-base * 1.5);\n  line-height: inherit;\n  color: @legend-color;\n  border: 0;\n  border-bottom: 1px solid @legend-border-color;\n}\n\n                      ",
                  }}
                />
              </div>
            </form>
          </div>
        </div>
      </section>

      <section className="col-lg-5 connectedSortable">
        <div className="box box-primary">
          <div className="box-header header-custom">
            <h3 className="box-title">Account Info</h3>
          </div>
          <div className="box-body form-horizontal">
            <p className="vg_total">
              {supplystore.inventory +
                `-` +
                supplystore.bill_type +
                `-` +
                supplystore.sell_type}
            </p>
            <div className="row">
              <div className="col-md-6">
                <fieldset>
                  <legend
                    align="center"
                    style={{
                      fontWeight: "bold",
                      backgroundColor: "crimson",
                      color: "white",
                    }}
                  >
                    Customer Due
                  </legend>
                  <div className="form-group ">
                    <label
                      htmlFor="accntname"
                      className="col-sm-5 control-label"
                    >
                      Amount
                    </label>
                    <div className="col-sm-7">
                      <input
                        type="text"
                        style={{
                          color: "Chartreuse",
                          fontWeight: "bold",
                          backgroundColor: "#5e8db5",
                          textAlign: "right",
                        }}
                        className="form-control"
                        disabled="true"
                        id="tpay"
                        name="tpay"
                        autoComplete="off"
                        placeholder={0}
                      />
                    </div>
                  </div>
                </fieldset>
              </div>
              <style
                type="text/css"
                dangerouslySetInnerHTML={{
                  __html:
                    "\n                        fieldset {\n  padding: 0;\n  margin: 0;\n  border: 0;\n\n  min-width: 0;\n}\n\nlegend {\n  display: block;\n  width: 100%;\n  padding: 0;\n  margin-bottom: @line-height-computed;\n  font-size: (@font-size-base * 1.5);\n  line-height: inherit;\n  color: @legend-color;\n  border: 0;\n  border-bottom: 1px solid @legend-border-color;\n}\n\n                      ",
                }}
              />
              <div className="col-md-6">
                <fieldset>
                  <legend
                    align="center"
                    style={{
                      fontWeight: "bold",
                      backgroundColor: "crimson",
                      color: "white",
                    }}
                  >
                    Side Stock
                  </legend>
                  <div className="form-group ">
                    <label
                      htmlFor="accntname"
                      className="col-sm-5 control-label"
                    >
                      PAYMENT
                    </label>
                    <div className="col-sm-7">
                      <input
                        type="text"
                        style={{
                          color: "Chartreuse",
                          fontWeight: "bold",
                          backgroundColor: "#5e8db5",
                          textAlign: "right",
                        }}
                        className="form-control"
                        disabled="true"
                        id="allpay"
                        name="allpay"
                        autoComplete="off"
                        placeholder={0}
                      />
                    </div>
                  </div>
                </fieldset>
              </div>
            </div>{" "}
            {/*  <input type="submit" name="create_pdf" class="btn btn-danger" value="Get Report" />   */}
            {/* /.row */}
          </div>
        </div>
      </section>
    </>
  );
};

export default AddForm;
