import React, { useState, useEffect, useRef, useContext } from "react";
import { Switch , Link, Route, useHistory, BrowserRouter } from "react-router-dom";
import Axios from "axios";
// import "react-toastify/dist/ReactToastify.css";

import { Context } from "../context/Store";
import AddForm from "./catalogue/AddForm";
import ViewList from "./catalogue/ViewList";

const Catalogue = () => {
  const history = useHistory();
  const API_URL = process.env.REACT_APP_SERVER_URL;
  useEffect(()=>{
		Axios.get(`${API_URL}/verify`)
		.then(res => {
			if(res.data.Status === "Success") {
        history.push("/catalogue")
			} else {
        history.push("/")
			}
		})
	}, [])

  // const {  accounts, ToastContainer, getShead, edit } =
  //   useContext(Context);

  return (
    <>
   
      <section className="content">
        <div className="row">
          <div className="col-xs-12">
            <AddForm />

            <ViewList />
          </div>
        </div>
      </section>
     
    </>
  );
};

export default Catalogue;
