import React, { useState, useEffect, useRef, useContext } from "react";
import { Context } from "../../context/Store";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { FiEdit,FiTrash2,FiPrinter } from 'react-icons/fi';
import { useDispatch, useSelector } from "react-redux";
import {loginUser} from "../../store/loginSlice";
import swal from "sweetalert";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import { selectData } from "../../store/supplySlice"; 
import { deleteData} from "../../store/supplySlice"; 
import { getAllsell } from "../../store/getDataSlice"; 
const ViewList = () => {
  const {  alldata_list} = useSelector((state) => state.getdata)
  const dispatch = useDispatch();


const deleteSell = (props) =>{
  
      // Display the popup menu using swal
      swal({
        text: 'Are you sure you want to submit?',
        buttons: {
          cancel: {
            text: 'Cancel',
            value: null,
            visible: true,
            className: '',
            closeModal: true,
          },
          confirm: {
            text: 'Submit',
            value: true,
            visible: true,
            className: '',
            closeModal: true,
          },
        },
      }).then((value) => {
        if (value) {
          dispatch(deleteData(props))
        }
      });
}

  const { ExportCSVButton } = CSVExport;
  const {
    selectsupplydata,
    comData,
    setSelldata,
    selldata,
    getsellData,
    selectlcselldata,
    deleteAllinv,

    serverUrl,
    lcPurchase,
   
  } = useContext(Context);

  const [deleteurl2, setDeleteurl] = useState(serverUrl + `/deletesellb2`);
  const [findurl, setFindurl] = useState(serverUrl + `/selectselldata`);

  var today = new Date();
  var dd = today.getDate();
  
  var mm = today.getMonth()+1; 
  var yyyy = today.getFullYear();
  if(dd<10) 
  {
      dd='0'+dd;
  } 
  
  if(mm<10) 
  {
      mm='0'+mm;
  } 
  today = yyyy+'-'+mm+'-'+dd;
  
      
      const dateMode = comData.date_mode;
      const serverDate = dateMode==0?today:comData.date;


// console.log(alldata_list);
  const lcselldata = alldata_list.filter(
    (selld) => selld.others_c == "ALLSP" && selld.date==serverDate && selld.inventory=="CRASH"
  );
  const MyExportCSV = (props) => {
    const handleClick = () => {
      props.onExport();
    };
    return (
      <div>
        <button className="btn btn-success" onClick={handleClick}>
          Export to CSV
        </button>
      </div>
    );
  };
  const columns = [
    {
      dataField: "inv_id",
      text: "INV",
      headerStyle: () => {
        return { width: "12%" };
      },
    },
    {
      dataField: "date",
      text: "DATE",
      headerStyle: () => {
        return { width: "10%" };
      },
    },
    {
      dataField: "name",
      text: "Account",
      headerStyle: () => {
        return { width: "20%" };
      },
    },
    {
      dataField: "v_no",
      text: "Vehicle",
      headerStyle: () => {
        return { width: "10%" };
      },
    },
    {
      dataField: "type",
      text: "Size",
      headerStyle: () => {
        return { width: "10%" };
      },
    },
    {
      dataField: "rate",
      text: "Rate",
      headerStyle: () => {
        return { width: "10%" };
      },
    },

    {
      dataField: "total",
      text: "Total",
      headerStyle: () => {
        return { width: "8%" };
      },
    },
    {
      dataField: "sell_type",
      text: "Inventory",
      headerStyle: () => {
        return { width: "8%" };
      },
    },
    {
      dataField: "id",
      text: "Action",
      formatter: (rowContent, row) => {
        return (
          <div className="ticon">
            <FiEdit
             variant="info"
             onClick={() => dispatch(selectData(row.id))}
            />
            {" "}
            <FiTrash2
            variant="danger"
            // onClick={() => deleteAllinv(row.id, deleteurl2)}
            />
           {" "}
            <FiPrinter 
            variant="primary"
              onClick={() => {
                deleteSell(row.id);             }
              }/>
           {" "}
          </div>
        );
      },
    },
  ];
  const rowStyle = (row, rowIndex) => {
    return { overflowWrap: "break-word", padding: "0px", textAlign: "left" };
  };

  const MySearch = (props) => {
    let input;
    const handleClick = () => {
      props.onSearch(input.value);
    };

    // useEffect(() => {
    //   if(editItem){
    //     getAccounts();

    // })

    return (
      <div>
        <input
          onChange={handleClick}
          className="form-control"
          style={{ backgroundColor: "white" }}
          ref={(n) => (input = n)}
          type="text"
        />
        {/* <button className="btn btn-warning" onClick={ handleClick }>Click to Search!!</button> */}
      </div>
    );
  };

  return (
    <>
      <section className="col-lg-12 connectedSortable">
        <div className="box box-primary">
          <div className="box-header header-custom">
            <h3 className="box-title">Detail Info</h3>
          </div>
          {/* /.box-header */}
          <div className="box-body">
            <ToolkitProvider
              keyField="id"
              data={lcselldata}
              columns={columns}
              search
              exportCSV
            >
              {(props) => (
                <div>
                  <MyExportCSV {...props.csvProps}>Export CSV!!</MyExportCSV>
                  <MySearch {...props.searchProps} />

                  <BootstrapTable
                    {...props.baseProps}
                    pagination={paginationFactory({ sizePerPage: 5 })}
                    rowStyle={rowStyle}
                    headerClasses="header-class"
                    headerStyle
                    striped
                    hover
                    dense
                  />
                </div>
              )}
            </ToolkitProvider>
          </div>
          {/* /.box-body */}
        </div>
        {/* /.box */}
      </section>
    </>
  );
};

export default ViewList;
